import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import 'bootstrap';

Vue.config.productionTip = false;

Vue.directive('bgimg', {
  bind(el, binding) {
    const packedUrl = require(`@/assets/image/${binding.value}`);
    el.style.backgroundImage = `url(${packedUrl})`;
  },
});

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
